.history_container {
    position: fixed;
    top: 0;
    left: 0;

    width: 50%;
    height: 100%;
    background-color: #a50;

    z-index: 3;
    overflow-y: scroll;

    display: none;
}

.history_item {
    display: inline-block;

    min-width: 2rem;
    margin: 1rem;

    font-size: 1rem;

    color: #fff;
}
