.search_form_container {
    width: calc(100%-1rem-5px);
    height: 3rem;
    margin: 1rem 5px 1rem 1rem;
    box-shadow: 0px 0px 5px 0px black;

    display: grid;
    grid-template-columns: 3rem 1fr 3rem;
    align-items: center;

    position: relative;
}

.search_form_text_input {
    width: calc(100%-0.5rem);
    height: 3rem;
    line-height: 2rem;
    font-size: 1.3rem;
    padding-left: 0.5rem;

    background: #fff;

    z-index: 1;
}

.search_form_btn {
    width: 100%;
    height: 3rem;

    background: var(--light_grey);
    transition: var(--fast_transition);
    cursor: pointer;

    font-size: 0.7rem;
    font-weight: 700;
    z-index: 2;
}
.search_form_btn:active {
    background: var(--grey);
    background: var(--yellow);
    color: #fff;
}

.history_btn {
    width: 100%;
    height: 3rem;
    /* background: rgb(221, 104, 104); */
}

.search_form_text_input_clear {
    position: absolute;
    top: 0.25rem;
    right: 5px;
    /* right: 3rem; */
    width: 2.5rem;
    height: 2.5rem;
    z-index: 2;
}
.search_form_text_input_clear_item1 {
    position: absolute;
    width: 60%;
    height: 2px;
    background-color: var(--light_grey);
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotateZ(45deg);
    transition: var(--fast_transition);
}
.search_form_text_input_clear_item2 {
    position: absolute;
    width: 60%;
    height: 2px;
    background-color: var(--light_grey);
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotateZ(-45deg);
    transition: var(--fast_transition);
}
.search_form_text_input_clear:hover {
    cursor: pointer;
}
.search_form_text_input_clear:hover .search_form_text_input_clear_item1 {
    background-color: var(--grey);
}
.search_form_text_input_clear:hover .search_form_text_input_clear_item2 {
    background-color: var(--grey);
}
