.dict_items{
    width: 100%;
    height: auto;
    list-style: none;
    margin: 0;
    padding: 0;
    white-space: pre-wrap;
    font-size: 11px;
    margin-top: 1rem;
    
}
.dict_item{
    width: 100%;
    height: auto;
    display: grid;
    grid-template-columns: 1fr 4fr;
    align-items: flex-start;
    border-top: black solid 1px;
}
