* {
    border: none;
    outline: none;
    margin: 0;
    padding: 0;
    margin: 0;
    padding: 0;
}

html,
body {
    box-sizing: border-box;
    font-size: 16px;

    --grey: rgb(120, 120, 120);
    --light_grey: rgb(222, 222, 222);
    --fast_transition: all 0.3s;
    --yellow: rgb(255, 153, 0);

    display: flex;
    justify-content: center;

    overflow: hidden;
}

#root {
    width: 95vw;
    height: 95vh;
    max-width: 40rem;

    position: relative;

    display: grid;
    grid-template-rows: 5rem 1fr 1fr;
}

.dict_container {
    width: calc(100%-1rem);
    max-height: 100%;
    padding-left: 1rem;
    overflow-y: scroll;

    background-color: #fff;

    z-index: 1;
}
.jisho_container {
    width: 100%;
    height: 150%;

    background-color: #fff;

    position: relative;
    top: -8rem;
    z-index: 0;
}

